.root {
    display: flex;
    height: 100%;
    background-color: #F9F9F9;
}

.content {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    height: 100%;
}

.wrapper {
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}

:global {
    html, body, #root {
        height: 100%;
        margin: 0;
    }
}



