.root {
    max-width: calc(100vw - 275px);
    padding: 0 15px;
    background: #304561;
    box-sizing: border-box;
    overflow: auto;
    max-height: 0;

    transition: max-height 0.3s cubic-bezier(.25,.8,.25,1);

    &.visible {
        max-height: 450px;
        transition: max-height 0.4s cubic-bezier(0.35, 0, 0.25, 1);
    }
    &.noTransition {
        transition: none;
    }

    @media (max-width: 900px) {
        max-width: 100vw;
    }
}
:global {
    pre {
        display: table;
        padding: 40px 0;
        margin: 0 auto;
        white-space: pre-wrap;

        .hljs-tag {
            white-space: pre;
        }
    }
    code {
        display: block;
        overflow-x: auto;
        padding: 0;
        color: white;
        font-family: Menlo, Monaco, "Andale Mono", "lucida console", "Courier New", monospace;
        line-height: 1.45;
        tab-size: 2;
        -webkit-font-smoothing: auto;
        -webkit-text-size-adjust: none;
        margin: 0;
    }

    .hljs-tag,
    .hljs-tag .hljs-title,
    .hljs-keyword,
    .hljs-literal,
    .hljs-strong,
    .hljs-change,
    .hljs-winutils,
    .hljs-flow,
    .nginx .hljs-title,
    .tex .hljs-special {
        color: #FFBC58;
    }

    code {
        color: #ddd;
    }

    code .hljs-constant,
    .asciidoc .hljs-code,
    .markdown .hljs-code {
        color: #80CBC4;
    }

    .hljs-code,
    .hljs-class .hljs-title,
    .hljs-header {
        color: white;
    }

    .hljs-link_label,
    .hljs-attribute,
    .hljs-symbol,
    .hljs-symbol .hljs-string,
    .hljs-value,
    .hljs-params,
    .hljs-regexp {
        color: #7DB9F4;
    }

    .hljs-link_url,
    .hljs-tag .hljs-value,
    .hljs-string,
    .hljs-bullet,
    .hljs-subst,
    .hljs-title,
    .hljs-emphasis,
    .hljs-type,
    .hljs-preprocessor,
    .hljs-pragma,
    .ruby .hljs-class .hljs-parent,
    .hljs-built_in,
    .django .hljs-template_tag,
    .django .hljs-variable,
    .smalltalk .hljs-class,
    .hljs-javadoc,
    .django .hljs-filter .hljs-argument,
    .smalltalk .hljs-localvars,
    .smalltalk .hljs-array,
    .hljs-attr_selector,
    .hljs-pseudo,
    .hljs-addition,
    .hljs-stream,
    .hljs-envvar,
    .apache .hljs-tag,
    .apache .hljs-cbracket,
    .tex .hljs-command,
    .hljs-prompt,
    .hljs-name {
        color: #9ccc65;
    }

    .hljs-comment,
    .hljs-annotation,
    .smartquote,
    .hljs-blockquote,
    .hljs-horizontal_rule,
    .hljs-decorator,
    .hljs-pi,
    .hljs-doctype,
    .hljs-deletion,
    .hljs-shebang,
    .apache .hljs-sqbracket,
    .tex .hljs-formula {
        color: #607D8B;
    }
}



