.root {
    display: flex;
    align-items: center;
    height: 60px;
    flex-shrink: 0;
    padding: 0 18px;
    background: #559FFF;
    color: #FFF;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
    font-weight: 500;
    font-size: 18px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    box-sizing: border-box;
}

.button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    background: rgba(255,255,255,0);
    border: 0;
    outline: none;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0,0,0,0);

    &:active {
        background: rgba(255,255,255,0.05);
    }
}

.menu {
    margin-right: 15px;

    @media (min-width: 900px) {
        display: none;
    }
}

.code {
    position: relative;
    margin-left: auto;

    @media (min-width: 900px) {
        &:after {
            content: attr(data-tooltip);
            position: absolute;
            top: 50%;
            margin-top: -17px;
            left: -5px;
            padding: 8px 15px;
            background-color: rgba(0,0,0,0.1);
            border-radius: 3px;

            color: #FFF;
            font-size: 14px;
            white-space: nowrap;
            transform: scale(0.5) translateX(-130%);
            opacity: 0;
            pointer-events: none;
            transition: transform 0.2s ease, opacity 0.2s ease;
        }
        &:hover:after {
            opacity: 1;
            transform: scale(1) translateX(-100%);
        }
    }
}



