.root {
    height: 100%;
    z-index: 1;

    @media (max-width: 800px) {
        &.visible {
            .nav {
                transform: translate3d(290px,0,0);
            }
            .backdrop {
                pointer-events: all;
                opacity: 1;
            }
        }
        .nav {
            margin-left: -290px;
            transform: translate3d(0,0,0);
            transition: transform 0.3s cubic-bezier(.25,.8,.25,1);
        }
        .backdrop {
            display: block;
        }
    }
}
.nav {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    width: 275px;
    height: 100%;
    background-color: #FFF;
    border-right: 1px solid #EFEFEF;

    color: #364149;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    box-sizing: border-box;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}

.header {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 60px;
    margin: 0;
    margin-bottom: 5px;
    padding: 0 15px;
    padding-right: 10px;
    border-bottom: 1px solid #EFEFEF;

    font-size: 18px;
    font-weight: 600;
    line-height: 0;

    a {
        color: inherit;
        text-decoration: none;
    }
    .repo {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-left: auto;
        border: 0;
        height: 28px;
        font-size: 14px;
        line-height: 22px;
        color: #333;
        text-decoration: none;
        text-shadow: 0 1px 0 #fff;

        .base {
            position: relative;
            z-index: 1;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            height: 100%;
            padding: 0 7px;
            border-radius: 4px;
            background-image: linear-gradient(to bottom,#fcfcfc 0,#eee 100%);
            border: 1px solid #d5d5d5;
            box-sizing: border-box;

            color: #333;
            font-size: 14px;
            font-weight: 600;
            line-height: 0;
            font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;

            &:focus {
                border-color: #51a7e8;
                outline: none;
                box-shadow: 0 0 5px rgba(81,167,232,0.5);
            }

            &:hover {
                background-image: linear-gradient(#eee, #ddd);
                border-color: #ccc;

                &:focus {
                    border-color: #51a7e8;
                }
            }
            &:active {
                background-color: #dcdcdc;
                background-image: none;
                border-color: #b5b5b5;
                box-shadow: inset 0 2px 4px rgba(0,0,0,0.15);
            }
        }
        .count {
            display: flex;
            padding-right: 5px;
            padding-left: 4px;
            align-items: center;
            justify-content: center;
            background: #FFF;
            height: 100%;
            font-size: 13px;
            border-top-right-radius: 4px;
            border-bottom-right-radius: 4px;
            border: 1px solid #d5d5d5;
            border-left: 0;
            box-sizing: border-box;

            &:hover {
                color: #4078c0;
            }
        }

        &.hasCount {
            .base {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
            }
        }

        img {
            margin-right: 4px;
        }
    }
}

.wrapper {
    overflow: auto;
    -webkit-overflow-scrolling: touch;
}

.list {
    padding: 0;
    padding-bottom: 20px;
    margin: 0;
    list-style: none;
    line-height: 1.5;

    dl {
        margin: 0;
    }
    strong {
        display: block;
        padding: 10px 15px;
    }
    dt {
        a {
            display: block;
            padding: 10px 30px;
            color: inherit;
            text-decoration: none;
            -webkit-tap-highlight-color: rgba(0,0,0,0.01);

            &.active, &:active {
                color: #008cff;
                background-color: #F8F8F8;
            }
        }
    }
}

.backdrop {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(33,33,33,0.4);
    display: none;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease;
}



