.root {
    position: relative;
    z-index: 0;
    min-height: calc(100vh - 60px);
    padding: 30px 0;
    border: 0;
    box-sizing: border-box;

    @media (min-height: 730px) {
        display: flex;
        align-items: center;
    }
}

.wrapper {
    display: table;
    margin: 0 auto;

    > div > caption {
        display: block;
        max-width: 320px;
        margin: 0 auto;
        margin-bottom: 20px;
        text-align: center;
        font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
        color: #888;
        -webkit-font-smoothing: antialiased;
        font-size: 15px;
        line-height: 1.3;
    }
}



